import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { Heading, Flex, Text, Skeleton, ChartIcon, CommunityIcon, SwapIcon } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'
import useTheme from 'hooks/useTheme'
import { formatLocalisedCompactNumber } from 'utils/formatBalance'
import { BigNumber } from 'bignumber.js'
import { ChainId } from '@pancakeswap/sdk'
import { getFarmApr } from 'utils/apr'
import { useFarms, usePriceCakeBusd } from 'state/farms/hooks'
import { FarmWithStakedValue } from 'views/Farms/components/FarmCard/FarmCard'
import { Farm } from 'state/types'
import IconCard, { IconCardData } from '../IconCard'
import StatCardContent from './StatCardContent'
import GradientLogo from '../GradientLogoSvg'




// Values fetched from bitQuery effective 6/8/21
const txCount = 38392695
const addressCount = 2319694
let tvl=0;
const Stats =() => {
  const { t } = useTranslation()
  
  const [tvlstate, setTvlstate] = useState(tvl)
  const cakePrice = usePriceCakeBusd()
  const { data: farmsLP, userDataLoaded } = useFarms()
  const activeFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.multiplier !== '0X')
  console.log(activeFarms.length)
 
  const farmsList = useCallback(
    
    (farmsToDisplay: Farm[]): FarmWithStakedValue[] => {
      tvl=0;
      console.log("calooooooooooo",farmsToDisplay.length)
      
      const farmsToDisplayWithAPR: FarmWithStakedValue[] = farmsToDisplay.map((farm) => {

        if (!farm.lpTotalInQuoteToken || farm.lpTotalInQuoteToken==="NaN" ||  !farm.quoteToken.busdPrice) {
          return farm
        }
        const totalLiquidity = new BigNumber(farm.lpTotalInQuoteToken).times(farm.quoteToken.busdPrice)
        tvl+=totalLiquidity.toNumber()
        console.log("888888888",farm.quoteToken.busdPrice)
        const { cakeRewardsApr, lpRewardsApr } = getFarmApr(new BigNumber(farm.poolWeight), cakePrice, totalLiquidity, farm.lpAddresses[ChainId.MAINNET])

        return { ...farm, apr: cakeRewardsApr, lpRewardsApr, liquidity: totalLiquidity }
      })
      setTvlstate(Math.round(tvl))
      return farmsToDisplayWithAPR
      
    },
    [cakePrice]
  )
   useEffect(() => {
    console.log("memooooooooooo")
    farmsList(activeFarms)
    
  },[activeFarms,farmsList])
  
  const { theme } = useTheme()

  const trades = formatLocalisedCompactNumber(txCount)
  const users = formatLocalisedCompactNumber(addressCount)

  const tvlText = t('And those users are now entrusting the platform with over $%tvl% in funds.', { tvl: tvlstate })
 
  const UsersCardData: IconCardData = {
    icon: <CommunityIcon color="secondary" width="36px" />,
  }

  const TradesCardData: IconCardData = {
    icon: <SwapIcon color="primary" width="36px" />,
  }

  const StakedCardData: IconCardData = {
    icon: <ChartIcon color="failure" width="36px" />,
  }

  return (
    <Flex justifyContent="center" alignItems="center" flexDirection="column">
      <GradientLogo height="48px" width="48px" mb="24px" />
      <Heading textAlign="center" scale="xl">
        {t('Used by millions.')}
      </Heading>
      <Heading textAlign="center" scale="xl" mb="32px">
        {t('Trusted with billions.')}
      </Heading>
      
     

      <Text textAlign="center" color="textSubtle" bold mb="32px">
        {t('Will you join them?')}
      </Text>

      <Flex flexDirection={['column', null, null, 'row']}>
        
        <IconCard {...StakedCardData}>
          <StatCardContent
            headingText={t('$%tvl% staked', { tvl: tvlstate })}
            bodyText={t('Total Value Locked')}
            highlightColor={theme.colors.failure}
          />
        </IconCard>
      </Flex>
      <br/>
      <br/>
    </Flex>
  )
}

export default Stats
